import React from "react";
import { Bar } from "react-chartjs-2";
import Chart from 'chart.js/auto';

import {
  Chart as ChartJS,
  BarElement,
  CategoryScale,
  LinearScale,
  PointElement

} from 'chart.js'

ChartJS.register(
  BarElement,
  CategoryScale,
  LinearScale,
  PointElement
)
getData()

async function getData() {
  const response = await fetch('population.csv');
  const data = await response.text();

  const table = data.split('\n').slice(1);
  table.forEach(row => {
    const columns = row.split(',');
    const Country = columns[0];
    const Migrants = columns[1];
    console.log(Country, Migrants);
  });
}
