    datasets: [
      {
        label: "Migrants",
        backgroundColor: [
          'rgba(69, 12, 122 )',
          'rgba(255, 159, 64)',
          'rgba(255, 205, 86)',
          'rgba(75, 192, 192)',
          'rgba(201, 203, 207)',
          'rgba(244, 17, 48)',
          'rgba(99, 128, 15)',
          'rgba(60, 99, 220)',
          'rgba(127, 178, 124)',
          'rgba(145, 130, 35)',
          'rgba(15, 40, 102 )'
          
        ],
       
        borderWidth: 5, 
        
        data: [-348399, -532687, 954806, -98955, -233379 ,21200,-60000, -369501,182456, -60000,71560 ,30000,-67152,-38033,-80000,283922 ]

      }
    ]
